import React, { useState } from 'react';

function Login({ onLogin }) {
  const [email, setEmail] = useState('user@mail.ru');
  const [password, setPassword] = useState('user123');
  const [error, setError] = useState('');

  //  
  const demoUsers = [
    { id: 1, email: 'admin@mail.ru', password: 'admin123', role: 'admin', name: '' },
    { id: 2, email: 'trainer@mail.ru', password: 'trainer123', role: 'trainer', name: ' ' },
    { id: 3, email: 'user@mail.ru', password: 'user123', role: 'user', name: ' ' }
  ];

  const handleSubmit = (e) => {
    e.preventDefault();
    setError('');
    
    //    
    const user = demoUsers.find(u => u.email === email && u.password === password);
    
    if (user) {
      onLogin({
        id: user.id,
        email: user.email,
        name: user.name,
        role: user.role
      });
    } else {
      setError(' email  ');
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-green-50">
      <div className="bg-white p-8 rounded-2xl shadow-xl w-96">
        <h1 className="text-3xl font-bold text-center text-gray-800 mb-2">
          
        </h1>
        <p className="text-center text-gray-600 mb-8">   (-)</p>
        
        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Email
            </label>
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder=" email"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <input
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder=" "
              required
            />
          </div>

          {error && (
            <div className="p-3 bg-red-50 text-red-700 rounded-lg text-sm">
              {error}
            </div>
          )}

          <div className="space-y-4">
            <button
              type="submit"
              className="w-full bg-blue-600 text-white py-3 rounded-lg font-medium hover:bg-blue-700 transition"
            >
              
            </button>

            <div className="text-sm text-gray-600 text-center">
              <p className="font-bold mb-2"> :</p>
              <p>: admin@mail.ru / admin123</p>
              <p>: trainer@mail.ru / trainer123</p>
              <p>: user@mail.ru / user123</p>
              <p className="mt-2 text-blue-600">   </p>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Login;